 padding: 8px;\r\n  bottom: 20px;\r\n  left: 0;\r\n  display: inline-block;\r\n  background-color: lightsalmon;\r\n  color: white;\r\n  border-radius: 0px 15px 15px 0px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {\r\n  margin-left: 10px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content {\r\n  padding: 16px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content h3 {\r\n  text-align: center;\r\n  padding: 18px;\r\n  border-bottom: 1px solid lightsalmon;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content h3 a {\r\n  color: lightsalmon;\r\n  text-decoration: none;\r\n  padding: 15px;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content h3 a:hover {\r\n  color: lightgreen;\r\n  text-decoration: none;\r\n  transition-duration: 0.5s;\r\n  scale:inherit;\r\n}\r\n\r\n.restaurant-item .restaurant-item__content p {\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  display: -webkit-box;\r\n  -webkit-line-clamp: 4; \r\n  -webkit-box-orient: vertical;\r\n}\r\n\r\n/*\r\n  Detail Restaurant\r\n*/\r\n\r\n.restaurant {\r\n  margin: 0 auto;\r\n  width: 100%;\r\n  max-width: 800px;\r\n  display: grid;\r\n  grid-template-columns: 1fr;\r\n  gap: 18px 16px;\r\n}\r\n\r\n.restaurant .restaurant__image {\r\n  width: 100%;\r\n  max-width: 400px;\r\n}\r\n\r\n.restaurant .restaurant__info h4 {\r\n  margin: 8px 0;\r\n}\r\n\r\n.restaurant__description {\r\n  grid-column-start: 1;\r\n  grid-column-end: 3;\r\n}\r\n\r\n/* \r\n  List Foods & Drinks\r\n*/\r\n\r\n.restaurant__menu ul li {\r\n  list-style-type: none;\r\n}\r\n\r\n\r\n.restaurant__menu .drinks {\r\n  margin-top: 20px;\r\n}\r\n\r\n/*\r\n  Footer\r\n*/\r\n\r\nfooter {\r\n  padding: 16px;\r\n  background-color: darkgrey;\r\n}\r\n\r\nfooter p {\r\n  text-align: center;\r\n  color: black;\r\n}\r\n\r\nfooter p a {\r\n  color: black;\r\n  text-decoration: none;\r\n  padding: 17px 0px;\r\n}\r\n\r\n/*\r\n   Favorite Button\r\n*/\r\n\r\n.like {\r\n  font-size: 18px;\r\n  bottom: 16px;\r\n  right: 16px;\r\n  color: white;\r\n  border: 0;\r\n  max-width: 1000px;\r\n  min-width: 200px;\r\n  width: 800px;\r\n  height: 55px;\r\n  cursor: pointer;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  border-radius: 15px;\r\n}\r\n\r\n.like:hover {\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  transition-duration: 0.3s;\r\n}\r\n\r\n#likeButtonContainer {\r\n  display: flex;\r\n  justify-content: center;\r\n  margin-top: 20px;\r\n}\r\n\r\n\r\n/* \r\n  Back Button\r\n*/\r\n\r\n.back {\r\n  grid-column-start: 1;\r\n  grid-column-end: 3;\r\n  height: 55px;\r\n  border-radius: 15px;\r\n  cursor: pointer;\r\n  font-size: 18px;\r\n  border: 0px;\r\n  background-color: darkgrey;\r\n}\r\n\r\n.back:hover {\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\r\n  transition-duration: 0.3s;\r\n}\r\n\r\n.back a {\r\n  color: white;\r\n  text-decoration: none;\r\n  padding: 17px 40%;\r\n  border-radius: 15px;\r\n}\r\n\r\n@media screen and (min-width: 650px) {\r\n  .app-bar {\r\n    grid-template-columns: 1fr ;\r\n    grid-template-rows: 1fr;\r\n    padding: 8px 32px;\r\n  }\r\n\r\n  .app-bar .app-bar__brand h1 {\r\n    font-size: 1.5em;\r\n  }\r\n\r\n  .app-bar .app-bar__menu {\r\n    display: none;\r\n  }\r\n\r\n  .app-bar .app-bar__navigation {\r\n    position: static;\r\n    width: 100%;\r\n  }\r\n\r\n  .app-bar .app-bar__navigation ul li {\r\n    display: inline-block;\r\n  }\r\n\r\n  .app-bar .app-bar__navigation ul li a {\r\n    display: inline-block;\r\n    width: 150px;\r\n    text-align: center;\r\n    margin: 0;\r\n  }\r\n\r\n  .app-bar .app-bar__navigation ul li a:hover {\r\n    border-bottom: 1px solid lightsalmon;\r\n    transition-duration: 0.6s; \r\n    color: lightsalmon;\r\n  }\r\n\r\n  .restaurants {\r\n    grid-template-columns: 1fr 1fr;\r\n\r\n  }\r\n\r\n  .restaurant {\r\n    grid-template-columns: auto 1fr;\r\n  }\r\n\r\n  .restaurant .restaurant__name {\r\n    grid-column-start: 1;\r\n    grid-column-end: 3;\r\n  }\r\n\r\n  .restaurant .restaurant__overview {\r\n    grid-column-start: 1;\r\n    grid-column-end: 3;\r\n  }\r\n}\r\n\r\n@media screen and (min-width: 800px) {\r\n  .app-bar .app-bar__brand h1 {\r\n    font-size: 2em;\r\n  }\r\n}\r\n\r\n@media screen and (min-width: 850px) {\r\n  .restaurants {\r\n    grid-template-columns: repeat(3, 1fr);\r\n  }\r\n}\r\n\r\n@media screen and (min-width: 1200px) {\r\n  .restaurants {\r\n    grid-template-columns: repeat(4, 1fr);\r\n  }\r\n}\r\n\r\n@media screen and (min-width: 1600px) {\r\n  .restaurants {\r\n    grid-template-columns: repeat(5, 1fr);\r\n  }\r\n}\r\n\r\n"],"sourceRoot":""}]);
